<template>
<section class="menu"> 
  <div class="container top">
  <div style="position:relative;left:20px"><img src="../assets/tp.png" alt="TRAKPAK"></div>    
  <router-link :class="[status==1 || status==null ? 'active':'inactive']"  :to="definitions">تعاریف</router-link>
  <router-link :class="[status==2 ? 'active':'inactive']"  :to="workstation">ایستگاه های کاری</router-link>
  <router-link :class="[status==3 ? 'active':'inactive']"  :to="userinformation">اطلاعات کاربری</router-link>
  <router-link :class="[status==4 ? 'active':'inactive']"  :to="report">گزارش</router-link>
  <router-link :class="[status==5 ? 'active':'inactive']"  :to="view">مشاهده</router-link>
  <router-link :class="[status==6 ? 'active':'inactive']"  :to="setting">تنظیمات</router-link>
  <p @click="showModal"  data-bs-toggle="modal" data-bs-target="#staticBackdrop"  :class="[status==7 ? 'active':'inactive']"  >خروج</p>
  </div>
</section>
<div class=" bottom ">
    <p><span>نام کاربر:</span> {{name}}</p>
    <p><span>سمت:</span> {{post}}</p>
    <p></p>
    <p></p>
    <p></p>
    <p id="time"></p>
    <p id="date"></p>
    
</div>

<div class="modal fade"  ref="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" >
  <div class="modal-dialog  modal-dialog-centered" >
    <div class="modal-content" style="background-color:#d6e6eb;border:2px solid #000000">
      <div class="modal-header" style="border: none;">
        <img src="../assets/exit1.png"  style="width:70px;height:70px;margin:auto"  alt="">
      </div>
      <div class="modal-body fs-4" style="margin:auto;border:none"> 
        کاربر گرامی آیا مایل به خروج هستید؟
      </div>
      <div class="modal-footer " style="margin: auto;border:none">
        <button @click="logout"  type="button" class="btn btn-danger mx-3 fs-5" data-bs-dismiss="modal" >بله</button>
        <button type="button" class="btn btn-dark mx-3 fs-5" data-bs-dismiss="modal">خیر</button>
      </div>
    </div>
  </div>
</div>
<router-view/>

</template>




<script>

var myVar = setInterval(myTimer, 1000);
function myTimer() {
    var d = new Date();
    document.getElementById("time").innerHTML = d.toLocaleTimeString();
}
import {Modal} from 'bootstrap'    
import { useRoute } from "vue-router";

export default {
    name:'MenuLayout',
    data:()=>({
        name:'علی حسینی',
        post:'اپراتور اتاق عمل',
        definitions:'/definitions',
        workstation:'/work-station',
        userinformation:'/user-information',
        report:'/report',
        view:'/view',
        setting:'/setting',
        exit:'/',
        status:null
    }),
    methods:{
        showModal(){
        this.modal.show();
        },
        logout(){
        this.$router.push('/');  
        }
    },
    mounted(){
      this.modal=new Modal(this.$refs.staticBackdrop)
    },
    updated() {
        const route = useRoute();
        if(route.path===this.definitions){
            this.status=1
        }
        if(route.path===this.workstation){
            this.status=2
        }
        if(route.path===this.userinformation){
            this.status=3
        }
        if(route.path===this.report){
            this.status=4
        }
        if(route.path===this.view){
            this.status=5
        }
        if(route.path===this.setting){
            this.status=6
        }
    }
}        
</script>



<style lang="scss" scoped>

*{
    font-family: yekan;
    direction: rtl;
}
section.menu{
    user-select: none;
    background-color: #3563B2;
    .top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: rgb(255, 255, 255);
        }
    img{
        width: 65px;
        height: 70px;
        margin: 10px 0;
    }
    }
.active{
    color:rgb(255, 255, 255);
    margin: 20px 0;
    font-size: 23px;
    text-decoration: none;
    border-bottom: 2px solid #41ff54;  
}
.inactive{
    color:rgb(255, 255, 255);
    margin: 20px 0;
    font-size: 23px;
    text-decoration: none;
    border-bottom: none;
    cursor: pointer;
}
.bottom{
    font-size:19px;
    margin:0.5rem 7rem;
    display: flex;
    justify-content: space-around;
    span{
        color: #3563B2;
    } 
}   
</style>